html {
  scroll-behavior: smooth; }

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font: 16px "Georgia" sans-serif;
  font-weight: regular; }

h1,h2, h3,h4,h5,h6 {
  margin: 0; }

* {
  margin: 0;
  padding: 0; }

p {
  margin: 0; }

@font-face {
  font-family: "Arial";
  src: url("../assets/fonts/arial/arial-regular.ttf");
  font-weight: regular;
  font-style: normal; }

@font-face {
  font-family: "Arial";
  src: url("../assets/fonts/arial/arial-italic.ttf");
  font-weight: italic;
  font-style: italic; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-regular.ttf");
  font-weight: regular;
  font-style: normal; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-bold.ttf");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-italic.ttf");
  font-weight: italic;
  font-style: italic; }

@font-face {
  font-family: "Georgia";
  src: url("../assets/fonts/georgia/georgia-bold-italic.ttf");
  font-weight: 600;
  font-style: italic; }

$main-color: #e5e5e5;
$color-light-xl: #ffffff;
$color-primary: #f1cdb3;
$color-primary-light: #fddcc4;
$color-light-s: #fafafa;
$color-light-l: #f6f6f6;
$color-dark-s: #cdcdcd;
$color-dark-3xl: #292929;
$color-dark-l: #545454;
$color-dark-xl: #4C4C4C;
$color-dark-m: #B2B2B2;
$start-screen-gradient-background: url(../assets/images/noise_transparent@2x.png), radial-gradient(237.5% 111.8% at 0% 0%, rgb(91, 72, 58) 0%, rgb(38, 36, 37) 100%);

//Styles for main-page

.wrapper {
  position: relative;
  height: 100%;
  margin: 0 auto; }

.header {
  position: absolute;
  width: 100%;
  max-width: 1200px;
  height: 60px;
  top: 30px;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 2;
  display: flex;
  justify-content: space-between;
  &__logo {
    text-decoration: none;
    &_title {
      display: block;
      font-family: Georgia;
      font-weight: 400;
      font-size: 32px;
      line-height: 35.2px;
      letter-spacing: 1.8px;
      color: $color-primary;
      margin-top: 0;
      margin-bottom: 10px; }
    &_subtitle {
      display: block;
      font-family: Arial;
      font-weight: 400;
      font-size: 13px;
      line-height: 14.95px;
      letter-spacing: 1.3px;
      color: $color-light-xl;
      margin: 0;
      margin-left: 4px; } }
  &__nav {
    display: flex;
    align-items: center; } }

.nav-menu {
  &__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 443px;
    padding: 0;
    padding-bottom: 4px; }
  &__list-item {
    font-size: 18px;
    list-style: none; }
  &__item {
    font-family: Arial;
    font-weight: 400;
    font-size: 15px;
    line-height: 26px;
    color: $color-dark-s;
    text-decoration: none;
    &:hover {
      color: $color-light-s;
      transition: all 0.3s; } }
  &__active {
    padding-bottom: 4px;
    border-bottom: 3px $color-primary solid;
    color: $color-light-s; } }

.burger {
  display: none; }

.start-screen {
  width: 100%;
  background: $start-screen-gradient-background;
  &__container {
    position: relative;
    height: 100%;
    padding: 0; } }

.container {
  width: 94%;
  max-width: 1200px;
  margin: 0 auto; }

.content-first {
  padding-top: 180px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  &__info {
    width: 38.4%;
    padding-bottom: 78px;
    height: 322px;
    &_title {
      width: 310px;
      font-family: Georgia;
      font-weight: 400;
      font-size: 44px;
      letter-spacing: 0.06em;
      line-height: 130%;
      color: $color-light-xl; }
    &_subtitle {
      font-family: Arial;
      font-weight: italic;
      font-size: 15px;
      line-height: 160%;
      color: $color-dark-s;
