@mixin btnPagination() {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 52px;
  height: 52px;
  border: 2px solid $color-dark-s;
  border-radius: 100px;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.3s; }

@mixin symbolBtnPagination() {
  font-family: Georgia;
  font-weight: 400;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  text-align: center; }

.pagination {
  &__disable {
    @include btnPagination();
    @include symbolBtnPagination();
    border: 2px solid $color-dark-s;
    cursor: not-allowed;
    color: $color-dark-s; }
  &__current-page {
    @include btnPagination();
    @include symbolBtnPagination();
    border: 2px solid $color-primary-light;
    background: $color-primary-light;
    cursor: default;
    color: $color-dark-3xl; }
  &__able {
    @include btnPagination();
    @include symbolBtnPagination();
    border: 2px solid $color-primary;
    cursor: pointer;
    color: $color-dark-3xl;
    &:hover {
      border: 2px solid $color-primary-light;
      background: $color-primary-light; } } }


@media (max-width: 1279px) {

