    width: 30px;
    position: fixed;
    top: 19px;
    right: 30px;
    height: 22px;
    z-index: 12;
    opacity: 1;
    cursor: pointer;
    span {
      display: block;
      height: 2px;
      width: 100%;
      background: $color-primary;
      margin-bottom: 8px;
      transition: 0.3s all;
      &:last-child {
        margin-bottom: 0px; } } } }

.burger_active {
  z-index: 11;
  span {
    margin-bottom: -2px;
    &:nth-child(1) {
      transform: translateY(3px) rotate(-45deg); }
    &:nth-child(2) {
      display: none; }
    &:nth-child(3) {
      transform: translateY(3px) rotate(45deg); } } }

